.ExpBox{
    max-width: 816px;
    margin-top: 50px;
}

.CompanyImage{
    width: 65px;
    height: 65px;
    border-radius: 50%;
    float: left;
    display: block;
    margin: -4px 18px 0 -80px;
    background-color: #fff;
    box-shadow: 0 10px 20px rgb(0 0 0 / 19%), 0 6px 6px rgb(0 0 0 / 23%);
    opacity: .8;
    z-index: 0;
}

.CompanyName{
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
    color: #f1f3f4 !important;
}

.CompanyPost{
    font-weight: bold;
    color: #f1f3f4 !important;
    font-size: 18px;
    letter-spacing: 1px;
}

.CompanyStatus{
    line-height: 30px;
    color: #f1f3f4 !important;
    font-size: 18px;
    letter-spacing: 1px;
}

.CompanyLoc{
    color: #f1f3f4 !important;
    font-size: 18px;
    letter-spacing: 1px;
}


.ExpWork{
    line-height: 30px;
    margin-top: 40px;
    color: #f1f3f4 !important;
    margin-bottom: 40px;
    font-size: 18px !important;
}

.ExpSkills{
    color: #f1f3f4 !important;
}

.ExpSkillSpan{
    font-size: 14px;
    border: 1px solid;
    border-radius: 3px;
    padding: 3px;
    margin: 0 1em 0.5em 0;
}