.footer{
    background: #fafafa;
    color: #333 !important;
    width: 100%;
    height: 70px;
    line-height: 70px;
    font-size: 14px;
    letter-spacing: .1em;
    text-align: center;
    -webkit-font-smoothing: antialiased;
}

.footer-ul{
    list-style-type: none !important;
}


.footer-ul-li{
    list-style: none !important;
    display: inline-block !important;
    margin: 0 15px !important;
}

.footer-link{
    color: #333 !important;
    text-decoration: none !important;
}