.HeaderBackground{
    background-color: #fafafa !important;
}

.HeaderName {
    color: #333;
    width: 100%;
    height: 100px;
    line-height: 100px;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: .2em;
    text-transform: uppercase;
    -webkit-font-smoothing: antialiased;
    display: unset;
}

.ContainerHeader {
    height: 80px;
}

.HeaderList {
    color: #333;
    width: 100%;
    height: 100px;
    line-height: 100px;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: .2em;
    text-transform: uppercase;
    -webkit-font-smoothing: antialiased;
    display: block;
}

.HeaderListDisplay{
    display: unset !important;
    color: #333;
}